<template>
  <div class="profile">

    <!-- Profile -->
    <form v-on:submit.prevent="submitForm">
      <div class="wrap_author">
        <AuthorSlot
          :profile="profileState"
          :state="displayState.state"
          @updateAvatar="onUpdateAvatar"
          @resetAvatar="resetAvatar"
          @updateGreetings="updateGreetings"
        >
        </AuthorSlot>
        <div class="wrap_btn-edit" v-if="profileState.nickname === profile.nickname">
          <button class="btn_edit" @click="displayState.state === 'edit' ? onUpdateGreetings() : onChangeState('edit')">
            {{ displayState.state === 'edit' ? '편집 완료' : '기본 정보 편집' }}
          </button>
        </div>
      </div>
    </form>

    <!-- Tab menu -->
    <div class="wrap_tab">
      <ul class="tab">
        <li 
          v-for="tab in tabs"
          :key="tab.name"
          :class="tab.name === displayState.tab ? 'isActive' : ''"
          @click="onChangeTab(tab.name)"
        >{{ tab.content }}</li>
      </ul>
    </div>

    <!-- Tab content - Introduce -->
    <div v-if="displayState.tab === 'intro'" class="introduce">
      <span v-if="displayState.state === 'introEdit'">
        <QuillEditor v-model:content="profileState.introduce" contentType="html" :options="options" />
      </span>

      <div
        v-else
        class="content_intro"
        v-dompurify-html="profileState.introduce ? profileState.introduce : '작성된 소개글이 없습니다.'"
      ></div>

      <Button
        v-if="profileState.nickname === profile.nickname"
        class="btn_edit_introduce"
        :content="displayState.state === 'introEdit' ? '편집 완료' : '소개글 편집'"
        :size="'sm'"
        :rounded="true"
        :bgColor="'primary-dark'"
        :customFontSize="'1.2rem'"
        @onClick="displayState.state === 'introEdit' ? onUpdateIntroduce() : onChangeState('introEdit')"
      ></Button>
    </div>

    <!-- Tab content - Posts -->
    <Suspense v-else>
      <template #default>
        <Posts :filter="displayState.tab" :userId="profileState._id" />
      </template>

      <template #fallback>
        <span>게시물 로딩 중...</span>
      </template>
    </Suspense>
  </div>
</template>

<script>
  import { defineComponent, onBeforeMount, inject, ref, reactive, computed } from 'vue'
  import { useRouter } from 'vue-router'
  import { useStore } from 'vuex'
  import { QuillEditor } from '@vueup/vue-quill'
  import AuthorSlot from '../../components/slotdata/AuthorSlot.vue'
  import Posts from '../../components/Posts.vue'
  import '@vueup/vue-quill/dist/vue-quill.snow.css'

  export default defineComponent({
    name: 'profile',
    components: {
      QuillEditor,
      AuthorSlot,
      Posts,
    },
    setup() {
      const { go, currentRoute: route } = useRouter()
      const { state, dispatch } = useStore()

      const TOAST_EL = inject('TOAST_EL')
      const tabs = ref([
        { name: 'intro', content: '소개글' },
        { name: 'like', content: '좋아요 한 게시물' },
        { name: 'comment', content: '댓글 단 게시물' }
      ])
      const options = ref({
        theme: 'snow',
        placeholder: '소개글을 작성해 주세요.',
        modules: {
          toolbar: [
            ['bold', 'italic', 'underline', 'strike'],
            ['blockquote', 'code-block'],
            [{ size: ['small', false, 'large', 'huge'] }],
            [{ header: [1, 2, 3, 4, 5, 6, false] }],
            [{ 'list': 'ordered'}, { 'list': 'bullet' }],
            [{ color: [] }, { background: [] }],
            [{ 'align': [] }],
            ['clean']
          ]
        }
      })

      const profileState = reactive({
        nickname: route.value.params.nickname,
        _id: '',
        avatar: '',
        greetings: '',
        introduce: '',
      })

      const displayState = reactive({
        tab: 'intro',
        state: 'view'
      })

      const profile = computed(() => state.auth.profile)
      const alignItems = computed(() => (displayState.state === 'edit' ? 'flex-start' : 'center'))

      const onChangeState = (state) => {
        displayState.state = state
      }

      const onChangeTab = (tab) => {
        displayState.tab = tab
      }

      const onUpdateAvatar = async (event) => {
        if (profile.value.nickname !== profileState.nickname) return TOAST_EL.value.open('error', '본인 프로필만 수정 가능합니다.')

        const formData = new FormData()
        formData.append('avatar', event.target.files[0])

        const { success, profile } = await dispatch('auth/updateProfileAvatar', { nickname: profileState.nickname, payload: formData })

        if (!success) return TOAST_EL.value.open('error', '아바타 업데이트 도중 에러가 발생하였습니다.')

        profileState.avatar = profile.avatar
      }

      const resetAvatar = async () => {
        if (profile.value.nickname !== profileState.nickname) return TOAST_EL.value.open('error', '본인 프로필만 수정 가능합니다.')

        const { success } = await dispatch('auth/deleteProfileAvatar', { nickname: profileState.nickname })

        if (!success) return TOAST_EL.value.open('error', '아바타 업데이트 도중 에러가 발생하였습니다.')

        profileState.avatar = null
      }

      const updateGreetings = (event) => {
        profileState.greetings = event.target.value
      }

      const onUpdateGreetings = async () => {
        if (profileState.greetings.length > 150) return alert('글자 수 제한을 초과하였습니다. (최대 150자까지 허용)')
        const { success, profile } = await dispatch('auth/updateProfile', {
          nickname: profileState.nickname,
          payload: { greetings: profileState.greetings },
        })

        if (!success) return

        profileState.greetings = profile.greetings
        onChangeState('view')
      }

      const onUpdateIntroduce = async () => {
        const { success, profile } = await dispatch('auth/updateProfile', {
          nickname: profileState.nickname,
          payload: {
            introduce: profileState.introduce.replace(/(^([ ]*<p><br><\/p>)*)|((<p><br><\/p>)*[ ]*$)/gi, '').trim(' '),
          },
        })

        if (!success) return

        profileState.introduce = profile.introduce
        onChangeState('view')
      }

      onBeforeMount(async () => {
        if (route.value.params.nickname) {
          const { success, profile } = await dispatch('auth/getProfile', { nickname: profileState.nickname })

          if (!success) return go(-1)

          profileState._id = profile._id
          profileState.avatar = profile.avatar
          profileState.greetings = profile.greetings
          profileState.introduce = profile.introduce
        }
      })

      return {
        tabs,
        options,
        profile,
        profileState,
        displayState,
        alignItems,
        onChangeState,
        onChangeTab,
        onUpdateAvatar,
        resetAvatar,
        updateGreetings,
        onUpdateGreetings,
        onUpdateIntroduce,
      }
    },
  })
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  .profile {
    .wrap_author {
      position: relative;
      margin: 0 0 4.8rem;

      &::v-deep .author {
        align-items: v-bind('alignItems');

        @include mobile {
          align-items: center;
        }
        .wrap_avatar {
          display: flex;
          flex-direction: column;
          align-items: center;
          .avatar {
            width: 9.6rem;
            height: 9.6rem;
          }

          .wrap_input_file {
            margin: 2.4rem 0 0.8rem;
            label {
              padding: 0.8rem 1.2rem;
              border-radius: 3.2rem;
              background: var(--primary);
              color: #fff;
              font-size: 1.1rem;
            }

            #input_file {
              display: none;
            }
          }
        }
        .wrap_nickname_greetings {
          color: var(--btn_text);
          width: 100%;
          .nickname {
            font-size: 1.6rem;
            font-weight: 500;
          }

          .greetings {
            margin: 1.6rem 0 0;
            font-size: 1.4rem;
            overflow: hidden;
            white-space: normal;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            white-space: pre-wrap;
          }

          .textarea_greetings {
            margin: 1.6rem 0 0;
            border: 0.1rem solid #ccc;
            border-radius: 1.2rem;
            padding: 1.2rem;
            width: 100%;
            min-height: 6.8rem;
            resize: none;
            font-size: 1.4rem;
            color: var(--text-light);

            @include mobile {
              min-height: 12rem;
            }
          }
        }
      }

      .btn_edit {
        position: absolute;
        top: 0;
        right: 0;
        font-size: 1.4rem;
        text-decoration: underline;
        color: var(--secondary);
      }
    }

    .wrap_tab {
      margin: 0 0 3.2rem 0;

      .tab {
        display: flex;
        flex-wrap: wrap;
        li {
          flex: 0 0 auto;
          padding: 0.8rem 1.6rem;
          margin: 0 1.6rem 0 0;
          list-style: none;
          text-align: center;
          border: 1px solid #ddd;
          border-radius: 3.2rem;
          transition: border-color ease 0.5s;
          cursor: pointer;
          color: var(--text-light);
          user-select: none;
          font-size: 1.4rem;
          letter-spacing: 0.1rem;
          font-family: 'Noto Sans KR';

          &:last-child {
            margin: 0;
          }

          &.isActive {
            border-color: var(--secondary);
            color: var(--secondary);
          }
        }

        @include mobile_all {
          flex-wrap: nowrap;
          overflow-x: auto;
          overflow-y: hidden;
        }
      }
    }
  }

  .introduce {
    font-size: 1.6rem;
    .content_intro {
      color: var(--btn_text);
    }

    .btn_edit_introduce {
      margin: 2.4rem 0 0;
      margin-left: auto;
    }
  }
</style>
